.icon {
    &__sale {
        font-size: 14px;
        line-height: 16px;
        color: $white;
        padding: 9px 16px;
        background: $primary-red;
        border-radius: 10px;
        position: absolute;
        top: 8px;
        left: 8px;
        margin: 0;
    }
    &__new {
        font-size: 14px;
        line-height: 16px;
        color: $primary-purple;
        display: inline-block;
        padding: 9px 16px;
        background: $primary-yellow;
        box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.1);
        border-radius: 10px;
        position: absolute;
        top: 8px;
        left: 8px;
        margin: 0;
    }
}