.btn-slider {
	width: 33px;
	height: 12px;
	position: absolute;
	bottom: 24px;
	left: 50%;
	transform: translateX(-50%);
	display: flex;
	justify-content: space-between;
    &__button {
        padding: 4px;
        border: 2px solid $white;
        cursor: pointer;
        background: transparent;
        border-radius: 50%;
        &.btn-slider__button-active {
            background: $white;
        }
    }
}