.arrow {
	width: 35px;
	height: 35px;
	border-radius: 50%;
	border: none;
	background-color: $white-transparency;
	background-image: url("../img/arrow_left.svg");
	background-repeat: no-repeat;
	background-position: center center;
	cursor: pointer;
	&.arrow__hover {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		&:hover {
			background-color: $white;
			transition-duration: 0.4s;
			transition-property: background-color;
		}
	}
	&.arrow__slider {
		&-left {
			left: 22px;
		}
	}
	&.arrow__slider {
		&-right {
			right: 22px;
			transform: translateY(-50%) scale(-1);
		}
	}
	&.arrow__review {
		&-left{
			left: 21.5px;
		}
		&-right {
			right: 21.5px;
			transform: translateY(-50%) scale(-1);
		}
	}
	&__paginator {
		&-left{
			margin-right: 8px;
		}
		&-right {
			transform: scale(-1);
		}
		&-left,
		&-right {
			&:hover{
				background-color: $grey-4-transparency;
				transition-duration: 0.4s;
				transition-property: background-color;
			}
		}
	}
}

@media (max-width: 767px) {
	.arrow {
		&.arrow__slider{
			&-left {
				left: 17px;
			}
		}
		&.arrow__slider{
			&-right {
				right: 14px;
			}
		}
	}
}