.reviews {
	position: relative;
	display: flex;
	background: $grey-5;
	border-radius: 16px;
	padding: 79px 68.5px 79px 68.5px;
    &__list {
        padding: 0 12.5px;
        width: 50%;
    }
    &__personal {
        padding: 16px;
        background: $white;
        box-shadow: 0px 16px 32px rgba(0, 0, 0, 0.1);
        border-radius: 16px;
        display: flex;
        align-items: self-start;
    }
    &__photo {
        margin-right: 29px;
        border-radius: 10px;
        max-width: 100%;
        height: auto;
    }
    &__text {
        font-size: 18px;
        line-height: 140%;
        color: $grey-2;
        margin: 0;
        max-width: 269px;
    }
} 