.input {
	display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    &__data {
        display: block;
        width: 50%;
        padding: 0 15px 23px 15px;
        & legend {
            padding-bottom: 22.5px;
        }
    }
    &__item {
        width: 100%;
        font-size: 16px;
        line-height: 19px;
        color: $grey-3;
        background: $grey-5;
        margin-bottom: 13px;
        border-radius: 12px;
        border: 2px solid transparent;
        padding: 13px;
        &:hover {
            border-color: $black-transparency;
            transition-duration: 0.4s;
            transition-property: border-color;
        }
        &:focus {
            outline: none;
            background: $white;
            border-color: $black-transparency;
        } 
    }
    &__comment { 
        font-family: 'Roboto', 'Tahoma', sans-serif;
        width: 100%;
        height: 117px;
        background: $grey-5;
        border-radius: 12px;
        font-size: 16px;
        line-height: 19px;
        color: $grey-3;
        border: 2px solid transparent;
        resize: none;
        padding: 15px 13px;
        &:hover {
            border-color: $black-transparency;
            transition-duration: 0.4s;
            transition-property: fill;
        }
        &:focus {
            outline: none;
            background: $white;
            border-color: $black-transparency;
        }
    }
}
.input__item::-webkit-outer-spin-button,
.input__item::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
.input__item {
    -moz-appearance: textfield;
}