.button-to-apply{
	cursor: pointer;
	font-family: 'Roboto', 'Tahoma', sans-serif;
	font-weight: 500;
	font-size: 14px;
	line-height: 16px;
	letter-spacing: 0.1em;
	text-transform: uppercase;
	color: $grey-1;
	padding: 8px 20px;
	background: $primary-yellow;
	border: none;
	border-radius: 8px;
	margin-top: 6px;
    &:hover {
        background: $primary-yellow-transparency-1;
        transition-duration: 0.4s;
        transition-property: background-color;
    }
    &:active {
        background: $primary-yellow-transparency-2;
        transition-duration: 0.4s;
        transition-property: background-color;
    }
}