.product {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px;
    &__catalog {
        width: 76.75%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding-top: 19px
    }
    &__list {
        &-w25 {
            width: 25%;
	        padding: 0 15px 48px 15px;
        }
        &-w33 {
            width: 33.333%;
            padding-bottom: 20.5px;
            padding-left: 30px;
        }
    }
    &__item {
        position: relative;
        background: $gradient-light-birch;
        border-radius: 16px;
        padding: 15px;
        &:hover {
            filter: drop-shadow(0px 8px 32px rgba(0, 0, 0, 0.12));
            transition-duration: 0.4s;
            transition-property: filter;
        }
        &-img {
            background: $white;
            border-radius: 8px;
            height: 268px;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        &-name {
            white-space: nowrap;
            display: flex;
            margin-top: 18px;
            font-size: 16px;
            line-height: 19px;
            color: $grey-1;
        }
    }
    &__price {
        display: flex;
        align-items: baseline;
        justify-content: space-between;
        padding: 10px 0 20px 0;
        &-old {
            font-size: 18px;
            line-height: 140%;
            color: $grey-1;
            margin: 0;
            position: relative;
            word-spacing: 4px;
            &::after {
                content: "";
                border-top: 1px solid $grey-1;
                position: absolute;
                width: 80px;
                top: 9px;
                left: 0;
            }
        }
        &-new {
            font-weight: bold;
            font-size: 22px;
            line-height: 26px;
            color: $black;
            margin:0 ;
            padding-right: 15px;
        }
    }
}
@media (max-width: 1257px) and (min-width: 1187px){
    .product {
        &__item {
            &-name {
                font-size: 15px;
            }
        }
    }
}

@media (max-width: 1186px) and (min-width: 1120px){
    .product {
        &__item {
            &-name {
                font-size: 14px;
            }
        }
    }
}
@media (max-width: 1119px) and (min-width: 1044px){
    .product {
        &__item {
            &-name {
                font-size: 13px;
            }
        }
    }
}
@media (max-width: 1043px) and (min-width: 992px){
    .product {
        &__item {
            &-name {
                font-size: 12px;
            }
        }
    }
}

@media (max-width: 1079px) and (min-width: 991px) {
	.product {
        &__price {
            &-new {
                padding: 0;
            }
        }
    }
}

@media (max-width: 991px) {
    .product {
        &__list {
            &-w25{
                width: 50%;
            }
        }
    }
}
@media (max-width: 767px) {
    .product {
        &__list {
            &-w25 {
                width: 100%;
                max-width: 480px;
                margin: 0 auto;
            }
        }
    }
}