.new-menu {
    display: inline-flex;
	align-items: center;
	margin: 1px 0 35px 0;
	padding: 6px 7px;
	border-radius: 29px;
	background: $gray-6;
    &__scroll {
        width: 100%;
        overflow: auto;
    }
    &__element {
        list-style: none;
    }
    &__button {
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        color: $grey-1;
        border:none;
        background: none;
        cursor: pointer;
        padding:10px 20px;
        &-active, 
        &:hover {
            color: $white;
            padding: 10px 20px;
            background: $gradient-purple-dark;
            border-radius: 32px;
        }
    }
}