.form {
	padding-bottom: 74px;
    &__page {
        display: flex;
        justify-content: space-evenly;
        padding-top: 27.5px;
    }
    &__radio {
        width: 25%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding-left: 4px;
        & fieldset {
            padding-bottom: 22px;
        }
    }
    &__list {
        display: flex;
        flex-direction: column;
        border: none;
        padding: 3px 0 0 0;
        margin: 0;
        & legend {
            font-weight: 500;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.1em;
            text-transform: uppercase;
            color: $grey-1;
        }
        &-text {
            font-weight: 500;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.1em;
            text-transform: uppercase;
            color: $grey-2;
            margin: 0;
            padding-bottom: 16px;
        }
        &-payment {
            font-weight: bold;
            font-size: 32px;
            line-height: 37px;
            color: $grey-1;
            margin: 0;
            padding-bottom: 33px;
        }
    }
    &__input {
        width: 50%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding-left: 15px;
    }
}