.catalog {
	display: flex;
	justify-content: space-between;
}
.filter {
    width: 23.25%;
    & h3 {
        font-weight: bold;
        font-size: 22px;
        line-height: 26px;
        color: $grey-1;
        padding: 20px 0;
        margin: 0;
    }
    &__price {
        padding-top: 29px;
        &-line {
            position: relative;
            width: 100%;
            height: 4px;
            background: $grey-5;
            border-radius: 15px;
        }
        &-button {
            position: absolute;
            width: 24px;
            height: 24px;
            border-radius: 50%;
            background: $grey-2;
            border:none;
            top: -10px;
            cursor: pointer;
            box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
            &:hover {
                background: $grey-1;
                transition-duration: 0.4s;
                transition-property: background-color;
            }
            &:active {
                background: $grey-4;
                transition-duration: 0.4s;
                transition-property: background-color;
            }
        }
    }
    &__input {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top: 27px;
        & span{
            width: 9px;
            border-top: 1px solid $grey-3;
        }
        &-price {
            font-size: 16px;
            line-height: 19px;
            color: $grey-2;
            outline: none;
            width: 100px;
            height: 51px;
            border: 2px solid transparent;
            border-radius: 12px;
            background: $grey-5;
            padding: 0 14px;
            
            &:focus {
                background: var(--white);
                border-color: var(--black-transparency);
            }
        }
    }
}
.filter__input-price::-webkit-outer-spin-button,
.filter__input-price::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
.filter__input-price {
    -moz-appearance: textfield;
}