.breadcrumbs {
	display: flex;
	align-items: center;
	margin: 0;
	padding: 3.5px 0 34px 0;
    & li {
        list-style: none;
        font-size: 16px;
        line-height: 19px;
        color: $grey-3;
        display: flex;
        align-items: center;
        &:not(:first-child)::before {
            content: "";
            display: flex;
            width: 24px;
            height: 24px;
            background-image: url("../img/arrow_right.svg");
            background-repeat: no-repeat;
            background-position: center center;
        }
    }
    & a {
        font-size: 16px;
        line-height: 19px;
        color: $grey-3;
        display: flex;
        align-items: center;
        padding-right: 11px;
    }
    & a:hover, & li:hover {
        color: $grey-1;
        transition-duration: 0.4s;
        transition-property: color;
    }
}