.checkbox {
	display: flex;
	flex-direction: column;
	border: none;
	padding-top: 16px;
    &__text {
        font-size: 16px;
        line-height: 19px;
        color: $grey-2;
        position: relative;
        padding-left: 28px;
        min-height: 40px;
        display: inline-flex;
        align-items: center;
        cursor: pointer;
        &::before {
            content: "";
            width: 28px;
            height: 28px;
            border: 2px solid $grey-1;
            border-radius: 8px;
            background: $white;
            position: absolute;
            left: -15px;
            top: 3px;
        }
        &::after {
            display: none;
            content: "";
            width: 20px;
            height: 20px;
            background-image: url("../img/checked.svg");
            background-repeat: no-repeat;
            position: absolute;
            top: 12px;
            left: -7px;
        }
    }
    &__category {
        &:checked {
            & + .checkbox__text {
                &::after {
                    display: block;
                }
            }
        }
        &:enabled {
            & + .checkbox__text {
                &:hover {
                    color: $grey-1;
                    transition-duration: 0.4s;
                    transition-property: color;
                }
            }
        }
        &:enabled {
            & + .checkbox__text {
                &:hover {
                    &::before {
                        background-color: $grey-5;
                        transition-duration: 0.4s;
                        transition-property: background-color;
                    }
                }
            
            }
        }
        &:disabled {
            & + .checkbox__text {
                opacity: 0.3;
                cursor: not-allowed;
            }
        }
        &:focus {
            & + .checkbox__text {
                &::before {
                    outline: 2px solid $black;
                }
            }
        }
    }
}