.radio{
    & legend {
        padding: 1px 0 13px 0;
    }
    &__text {
        position: relative;
        font-size: 16px;
        line-height: 19px;
        color: $grey-2;
        align-items: center;
        display: inline-flex;
        padding-bottom: 6px;
        cursor: pointer;
        padding-left: 42px;
        min-height: 38px;
        &::before{
            content: "";
            width: 28px;
            height: 28px;
            border: 2px solid $grey-1;
            border-radius: 50%;
            position: absolute;
            left: 0;
            top: 0;
            background-color: $white;
        }
        &::after{
            display: none;
            content: "";
            width: 14px;
            height: 14px;
            background: $primary-purple;
            border-radius: 50%;
            position: absolute;
            left: 9px;
            top: 9px;
        }
    }
    &__default {
        &:checked {
            & + .radio__text {
                &::after{
                    display: block;
                }
            }
        }
        &:enabled {
            & + .radio__text {
                &:hover{
                   color: $grey-1;
                }
            }
        }
        &:enabled {
            & + .radio__text {
                &:hover {
                    &::before{
                        background: $grey-5;
                    }
                }
            }
        }
        &:disabled {
            & + .radio__text {
                opacity: 0.3;
                cursor: not-allowed;
            }
        }
        &:focus {
            & + .radio__text {
                &::before{
                    outline: 2px solid $black;
                }
            }
        }
    }
}