.slider {
    margin-bottom: 52px;
	position: relative;
    &__slide{
        background: $primary-yellow;
        border-radius: 16px;
        color: $white;
        display: flex;
        justify-content: space-around;
        padding: 40px 22px 38px;
    }
    &__text {
        width: 39%;
        padding-top: 31px;
        padding-left: 22px;
        & h2 {
            font-weight: bold;
            font-size: 40px;
            line-height: 47px;
            margin: 0;
            padding-bottom: 11px;
        }
        & p {
            font-size: 18px;
            line-height: 140%;
            margin: 0;
            padding-bottom: 30px;
        }
    }
    &__img {
        width: 40%;
        display: flex;
        justify-content: center;
        padding-left: 27px;
    }
}

@media (max-width: 767px) {
    .slider {
        &-nowrapper {
            padding: 0;
        }
        &__slide {
            border-radius: 0;
            flex-direction: column;
        }
        &__text {
            width: 100%;
            & h2 {
                justify-content: center;
            }
        }
        &__img {
            width: 100%;
            order: -1;
        }
    }
}