.back {
	font-style: normal;
	font-weight: 500;
	font-size: 14px;
	line-height: 16px;
	letter-spacing: 0.1em;
	text-transform: uppercase;
	color: $grey-1;
	padding-top: 12px;
	display: inline-flex;
	align-items: center;
    &::before {
        content: "";
        display: flex;
        background-image: url("../img/back-icon.svg");
        width: 24px;
        height: 24px;
        margin-right: 8px;
    }
    &:hover {
        color: $primary-purple;
        transition-duration: 0.4s;
        transition-property: color;
    }
}