.footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 24px 0 22px;
	border-top: 1px solid $grey-5;
    &__list {
        display: flex;
        align-items: center;
    }
    &__logo {
        display: flex;
        margin-right: 60px;
    }
    &__menu {
        display: flex;
        margin: 0;
        padding: 0;
        & li {
            list-style: none;
            padding-right:  36px;
        }
        & a {
            font-weight: 500;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.1em;
            text-transform: uppercase;
            color: $grey-1;
            &:hover {
                color: $primary-purple;
                transition-duration: 0.4s;
                transition-property: color;
            }
            &:active {
                color: $primary-purple-transparency-1;
                transition-duration: 0.4s;
                transition-property: color;
            }
        }
    }
    &__socials {
        display: flex;
        padding-right: 3px;
        padding-top: 3px;
        align-items: center;
        & a {
            margin-left: 32px;
        }
        &-instagram {
            &:hover path {
            fill: $red-instagram;
            transition-duration: 0.4s;
            transition-property: fill;
            }
        }
        &-fecebook {
            :hover path[fill="#858585"] {
            fill: $blue-fecebook;
            transition-duration: 0.4s;
            transition-property: fill;
            }
        }
        &-telegram {
            :hover path[fill="#858585"] {
            fill: $blue-telegram;
            transition-duration: 0.4s;
            transition-property: fill;
            }
        }
    }
    &__copyright {
        font-size: 12px;
        line-height: 14px;
        text-align: center;
        color: $grey-3;
        margin: 0;
        padding-bottom: 22px;
    } 
}