.paginator {
	display: flex;
	align-items: center;
	padding: 26.5px 0 97px 29px;
    &__item,
    &__span {
        width: 32px;
        height: 32px;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        color: $grey-1;
        background: transparent;
        border: 1px solid #DFE3E8;
        border-radius: 4px;
        margin-right: 8px;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__span {
        cursor: default;
    }
    &__item,
    &__item-active {
        &:hover {
            color: $primary-purple;
            border-color: $primary-purple;
            transition-duration: 0.4s;
            transition-property: color, border;
        }
    }
}