.map {
	padding-top: 100px;
    &__frame {
        border: 0;
        width: 100%;
        height: 500px;
    }
    &__list {
        display: flex;
        align-items: center;
        padding: 24px 0 34px 0;
    }
    &__address {
        font-weight: bold;
        font-size: 22px;
        line-height: 26px;
        color: $grey-1;
        padding-right: 20px;
        padding-left: 2px;
        display: flex;
        align-items: center;
        margin: 0;
        &::before {
            content: "";
            display: flex;
            width: 24px;
            height: 24px;
            background-image: url("../img/location.svg");
            background-repeat: no-repeat;
            background-position: center center;
            margin-right: 6px;
        }
    }
    &__location {
        font-size: 18px;
        line-height: 140%;
        color: $grey-1;
        margin: 0;
    }
}